import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { Button, Container, Grid, Typography } from '@mui/material';
import axios from 'axios';

const ContactsIndex = () => {
    const [contacts, setContacts] = useState([]);

    useEffect(() => {
        axios.get('http://laravel-test.test/api/contacts')
            .then(response => {
                setContacts(response.data.data);
            })
            .catch(error => {
                console.log('Error fetching contacts:', error);
            });
    }, []);

    const handleDelete = (id) => {
        axios.delete(`http://laravel-test.test/api/contacts/${id}`)
            .then(() => {
                setContacts(contacts.filter(contact => contact.id !== id));
            })
            .catch(error => {
                console.log('Error deleting contact:', error);
            });
    };

    return (
        <Container>
            <Typography variant="h4" gutterBottom>Contacts List</Typography>
            <Grid container spacing={3}>
                {contacts.map(contact => (
                    <Grid item xs={12} md={6} lg={4} key={contact.id}>
                        <div>
                            <Typography variant="h6">{contact.name}</Typography>
                            <Typography variant="body2">{contact.email}</Typography>
                            {/* <Button component={Link} to={`/contacts/${contact.id}`} variant="outlined">View</Button>
                            <Button onClick={() => handleDelete(contact.id)} color="error" variant="contained">Delete</Button> */}
                        </div>
                    </Grid>
                ))}
            </Grid>
            {/* <Button component={Link} to="/contacts/add" variant="contained" color="primary">Add Contact</Button> */}
        </Container>
    );
};

export default ContactsIndex;
